<template>
  <div id='receive_money' class="card  card-container by-age-money">
    <div class="card-body">
      <h5 class="card-body-title">the years number of rest of your lifetime:</h5>
      <input id='txt_age' type="number" step="1" min="0" class="form-control txt-age" name="" value="30" placeholder="inpu a number of rest of your lifetime" v-on:blur='checkAge'/>
      <button class="btn btn-primary btn-next" type="button" name="button" id='btn_money_to_next' v-on:click='this.toNext'>NEXT</button>
    </div>
    <ToaskMessage :content='this.errorContent' v-bind:shown='this.showCount'/>
  </div>
</template>

<script>
  import qs from 'qs';
  import router from '@/router'
  import axios from '@/js/axios.js';
  import ToaskMessage from '@/components/toaskMessage.vue'
  export default {
    name: 'ReceiveMoneyByAge',
    data () {
      return {
        age: 30,
        errorContent: 'get your money faile',
        showCount: 0
      }
    },
    methods: {
      toNext: function () {
        let me = this;
        if (me.checkAge() === false) return

        let user = this.$commonUtil.getUser();

        let data = {};
        data.bidderId = user.id
        data.age = document.getElementById('txt_age').value

        axios.post(
          '/api/bidderMoney/create2',
          qs.stringify(data),
          {headers: {'Content-Type': 'application/x-www-form-urlencoded'}}
        ).then(result => {
          let code = result.data.code;
          if (code !== 0) {
            me.errorContent = 'create money faile'
            me.showCount++;
            return;
          }

          let nextPage = me.$commonUtil.getNextPageKey('receiveMoneyByAge');
          router.replace({
            path: nextPage,
            query: {
              money: result.data.data
            }
          });
        }).catch(function (err) {
          console.log(err);
          me.errorContent = 'create money faile'
          me.showCount++;
        })
      },
      checkAge: function () {
        let val = document.getElementById('txt_age').value - 0
        if (val === 0 || isNaN(val)) {
          this.errorContent = 'age must be a number'
          this.showCount++
          return false
        }
        if (val <= 0) {
          this.errorContent = 'age must be greater than zero'
          this.showCount++
          return false
        }
        return true
      }
    },
    components: {
      ToaskMessage: ToaskMessage
    }
  }
</script>

<style scoped>
  .card-body-title {
    margin-top: 12%;
    margin-bottom: 5%;
    color: #333333;
  }
  .txt-age {
    margin-top: 1rem;
    height: 3.0rem;
    color: #989898;
  }
  #btn_money_to_next {
    margin-top: 10%;
    width: 100%;
    height: 3rem;
    background-color: #DD9622;
    border: 0px solid #DD9622;
  }

  ::-webkit-input-placeholder {
    color: #E6E6E6;
  }
  :-moz-placeholder {
    color: #E6E6E6;
  }
  ::-moz-placeholder {
    color: #E6E6E6;
  }
  :-ms-input-placeholder {
    color: #E6E6E6;
  }
</style>



