import CommonUtil from '@/js/CommonUtil.js'
import WebSocketUtils from '@/js/WebSocketUtils.js'
let MessageUtils = {

    HEART_BEAT: 0,

    RESPONSE_TEXT: 1,

    SYSTEM_TEXT: 2,

    CHANGE_LOT: 3,

    AUCTION_OVER: 4,

    OFFER_PRICE: 5,

    onMessageArrival: function (msg) {},

    onChangeLot: function (logId) {},

    onAuctionOver: function () {},

    onOfferPrice: function (text) {},

    init: function () {
        let me = this;
        WebSocketUtils.createSocket();
        WebSocketUtils.messageArrival = function (msgs) {
            // console.log("data arrival:", msgs);
            me.onMessageArrival(msgs);
            let messages = JSON.parse(msgs);
            for (let index in messages) {
              let message = JSON.parse(messages[index]);
              // console.log("message:", message);
              if (Array.isArray(message)) {
                // console.log("message is Array:", message);
                for (let idx in message) {
                  let msg = JSON.parse(message[idx]);
                  // console.log("msg:", msg);
                  me.dispatchMessage(msg);
                }
              } else if (typeof message === 'object') {
                // console.log("message is object:", message);
                me.dispatchMessage(message);
              }
            }
        }
    },

    dispatchMessage: function (message) {
      // let message = JSON.parse(messages[index]);
      if (message.msgType === MessageUtils.CHANGE_LOT) {
        let msg = JSON.parse(message.text);
        this.onChangeLot(msg);
      } else if (message.msgType === MessageUtils.AUCTION_OVER) {
        this.onAuctionOver();
      } else if (message.msgType === MessageUtils.OFFER_PRICE) {
        let msg = JSON.parse(message.text);
        this.onOfferPrice(msg);
      }
    },

    send: function (message) {
        let msg = JSON.stringify(message);
        WebSocketUtils.send(msg);
    },

    sendMessage: function (msgType, sessionId, userType, msg) {
        let id = new Date().getTime();
        let message = this.generateMessage(id, msgType, sessionId, userType, msg);
        this.send(message);
    },

    testNextLotMessage: function (msg) {
        let id = new Date().getTime();
        let msgType = this.CHANGE_LOT;
        let sessionId = CommonUtil.getUserSession();
        let userType = 0;
        let message = this.generateMessage(id, msgType, sessionId, userType, msg);
        console.log('send test message', message);
        this.send(message);
    },

    testAuctionOverMessage: function (msg) {
        let id = new Date().getTime();
        let msgType = this.AUCTION_OVER;
        let sessionId = CommonUtil.getUserSession();
        let userType = 0;
        let message = this.generateMessage(id, msgType, sessionId, userType, msg);
        console.log('send test message', message);
        this.send(message);
    },

    generateHeartBeatMessage: function () {
        let user = CommonUtil.getUser();
        let id = new Date().getTime();
        let userType = user.userType;
        let sessionId = CommonUtil.getUserSession();
        let message = this.generateMessage(id, this.HEART_BEAT, sessionId, userType, "");
        return JSON.stringify(message);
    },

    generateMessage: function (id, msgType, sessionId, userType, msg) {
        let message = {};
        message.id = id;
        message.msgType = msgType;
        message.sessionId = sessionId;
        message.userType = userType;
        message.text = msg;
        return message;
    }

}

export default MessageUtils;



